import React from "react";
import EntertimentLabel from "./EntertimentLabel";
import allEntirementsStyle from "../../styles/allEntertiments.module.css";
import ProductCard from "./ProductCard";

export default function AllEntertiments() {
  return (
    <div className={allEntirementsStyle.container}>
      <EntertimentLabel></EntertimentLabel>
      
        <ProductCard tag={'all'}/>
     
      <div className={allEntirementsStyle.entertiment_button}>
        <a href=""> </a>
      </div>
    </div>
  );
}
